import { createWebHistory, createRouter } from "vue-router";
import Home from "@/views/Home";
import About from "@/views/About";
import Login from "@/views/Login";
import JudulPA from "@/views/JudulPA";
import Karya from "@/views/Karya";
import RisetGrup from "@/views/RisetGrup";
import DetailKarya from "@/views/Detail/DetailKarya"
import DetailPA from "@/views/Detail/DetailPA"
import DetailPenelitian from "@/views/Detail/DetailPenelitian.vue"

import HomeAdmin from "@/views/RisetGrup/Home";
// import AboutRG from '@/views/RisetGrup/About'


const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/judulpa",
    name: "JudulPA",
    component: JudulPA,
  },
  {
    path: "/judulpa/:id",
    name: "detailPA",
    component: DetailPA,
  },
  {
    path: "/karya",
    name: "Karya",
    component: Karya,
  },
  {
    path: "/karya/:namakarya",
    name: "DetailKarya",
    props:true,
    component: DetailKarya,
  },
  {
    path: "/risetgrup",
    name: "RisetGrup",
    component: RisetGrup,
  },
  {
    path: "/risetgrup/:namarg/penelitian/:id",
    props:true,
    name:"DetailPenelitian",
    component: DetailPenelitian,
  },
  {
    path: "/risetgrup/:namarg",
    component: HomeAdmin,
    props:true,
    name:"risetgrups",
    children: [
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: "posts",
        // component: UserPosts,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
